.map iframe {
  width: 100%;
  height: 350px;
  display: block;
  border: none;
}

.map__img {
  display: none;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.map__img img {
  display: block;
  width: 100%;
  height: 100%;

  object-fit: cover;
  object-position: center;
}

.no-js iframe {
  display: none;
}

.no-js .map__img {
  display: block;
}
