.main-nav__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;

  display: none;

  background-color: #81b3d2;

  @media (min-width: $tablet-width) {
    position: static;

    display: grid;

    background-color: #ffffff;

    grid-template-columns: repeat(4, auto);
  }
}

.main-nav__list::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;

  height: 7px;

  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.2) 100%
  );

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav__item {
  padding: 17px 17px 13px;

  text-align: center;

  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);

  @media (min-width: $tablet-width) {
    width: 153px;

    box-shadow: none;
  }

  @media (min-width: $desktop-width) {
    width: 239px;
  }

  &:nth-child(4n-2) {
    @media (min-width: $tablet-width) {
      margin-right: 76px;
    }

    @media (min-width: $desktop-width) {
      margin-right: 120px;
    }
  }

  &:nth-child(4n-1) {
    @media (min-width: $tablet-width) {
      margin-left: 76px;
    }

    @media (min-width: $desktop-width) {
      margin-left: 120px;
    }
  }
}

.main-nav__link {
  display: inline-block;

  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }

  &:hover,
  &:focus {
    text-decoration: underline;

    outline: none;

    @media (min-width: $tablet-width) {
      color: #81b3d2;
      text-decoration: none;
    }
  }

  &:active {
    color: rgba($color: #000000, $alpha: 0.3);
  }
}

.main-nav__link--current {
  color: #766357;
}

.main-nav--open .main-nav__list {
  display: grid;
}

.main-nav--open .main-nav__toggle {
  background-image: url("../img/icon-cross.svg");
}

.no-js .main-nav__toggle {
  display: none;
}

.no-js .main-nav__list {
  position: relative;

  display: grid;

  @media (min-width: $tablet-width) {
    position: static;
  }
}
