@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
*,
::after,
::before {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  background-color: #e7e5e5;
}

.body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  font-size: 18px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  max-width: 1200px;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.button {
  display: block;
  width: 284px;
  margin: 0 auto;
  padding: 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
}

.button:hover {
  background: #669ec0;
}

.button:active,
.button:focus {
  background: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}

.button:disabled {
  background: #e5e5e5;
}

.header {
  min-height: 56px;
}

.main-nav {
  position: relative;
}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 10;
  width: 23px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:hover, .main-nav__toggle:focus {
  outline: none;
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  display: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: grid;
    background-color: #ffffff;
    grid-template-columns: repeat(4, auto);
  }
}

.main-nav__list::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__list::after {
    display: none;
  }
}

.main-nav__item {
  padding: 17px 17px 13px;
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    width: 239px;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(4n-2) {
    margin-right: 76px;
  }
}
@media (min-width: 1200px) {
  .main-nav__item:nth-child(4n-2) {
    margin-right: 120px;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(4n-1) {
    margin-left: 76px;
  }
}
@media (min-width: 1200px) {
  .main-nav__item:nth-child(4n-1) {
    margin-left: 120px;
  }
}

.main-nav__link {
  display: inline-block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}
.main-nav__link:hover, .main-nav__link:focus {
  text-decoration: underline;
  outline: none;
}
@media (min-width: 768px) {
  .main-nav__link:hover, .main-nav__link:focus {
    color: #81b3d2;
    text-decoration: none;
  }
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.main-nav__link--current {
  color: #766357;
}

.main-nav--open .main-nav__list {
  display: grid;
}

.main-nav--open .main-nav__toggle {
  background-image: url("../img/icon-cross.svg");
}

.no-js .main-nav__toggle {
  display: none;
}

.no-js .main-nav__list {
  position: relative;
  display: grid;
}
@media (min-width: 768px) {
  .no-js .main-nav__list {
    position: static;
  }
}

